import { useState } from "react";
import "./styles.css";

export default function App() {
  const [da, setDa] = useState();
  const [ca, setCa] = useState();
  const [isCalculated, setIsCalculated] = useState(false);

  const handleCalculation = () => {
    const daTime = 288;
    const caTime = 210;
    const totalTime = 28800;
    if (da > 0) {
      let timeTaken = daTime * da;
      let remainingTime = totalTime - timeTaken;
      let caCases = (remainingTime / caTime).toFixed(2);
      setCa(caCases);
    } else if (ca > 0) {
      let timeTaken = caTime * ca;
      let remainingTime = totalTime - timeTaken;
      let daCases = (remainingTime / daTime).toFixed(2);
      setDa(daCases);
    }
    setIsCalculated(true);
  };

  const handleReset = () => {
    setIsCalculated(false);
    setCa(0);
    setDa(0);
  };

  return (
    <div className="App">
      <h1>RA Target Calculator</h1>
      <div style={{ marginBottom: "50px" }}>
        <h3>DA</h3>
        <div
          style={{
            border: "1px solid black",
            width: "200px",
            margin: "auto",
            marginBottom: "10px"
          }}
        >
          <div style={{ borderBottom: "1px solid black" }}>
            Daily Target : 100 ASINs
          </div>
          <div>AHT/Case : 4.8 mins</div>
        </div>
        <input
          type="number"
          value={da}
          onChange={(e) => {
            setDa(e.target.value);
          }}
          placeholder="ASIN count"
        />
      </div>
      <div>
        <h3>CA</h3>
        <div
          style={{
            border: "1px solid black",
            width: "200px",
            margin: "auto",
            marginBottom: "10px"
          }}
        >
          <div style={{ borderBottom: "1px solid black" }}>
            Daily Target : 135 ASINs
          </div>
          <div>AHT/Case : 3.55 mins</div>
        </div>
        <input
          type="number"
          value={ca}
          onChange={(e) => {
            setCa(e.target.value);
          }}
          placeholder="ASIN count"
        />
      </div>
      <br />
      {!isCalculated && <button onClick={handleCalculation}>Calculate</button>}
      <br />
      <br />
      <button onClick={handleReset}>Reset</button>
    </div>
  );
}
